<template>
  <div>
    <v-snackbar
      v-model="snackbarShown"
      :color="currentNotification.type"
      :multi-line="true"
      :timeout="3000"
      :vertical="false"
    >
      <span v-if="currentNotification">{{currentNotification.name, currentNotification.args}}</span>
      <v-btn dark flat @click="snackbarShown = false">{{$t("common.interaction.close")}}</v-btn>
    </v-snackbar>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  data() {
    return {
      snackbarShown: false
    };
  },
  watch: {
    snackbarShown: {
      handler() {
        if (!this.snackbarShown) {
          this.$nextTick(() => {
            this.$store.dispatch("notifications/closeCurrent");
          });
        }
      }
    },
    currentNotification: {
      immediate: true,
      handler() {
        if (this.currentNotification) {
          this.snackbarShown = true;
        }
      }
    }
  },
  methods: {},
  computed: {
    ...mapGetters({
      currentNotification: "notifications/currentNotification"
    })
  }
};
</script>