// import "@babel/polyfill";
import "babel-polyfill";

import Vue from "vue";
import "./plugins/vuetify";
import i18n from "./plugins/i18n";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import "roboto-fontface/css/roboto/roboto-fontface.css";
import "@mdi/font/css/materialdesignicons.css";
import { AxiosResponse, AxiosError } from "axios";
import { IServerResponse, INotification } from "./store/notifications";

Vue.config.productionTip = false;

Vue.prototype.$bNotify = function(
  notification:
    | AxiosResponse<IServerResponse>
    | AxiosError
    | IServerResponse
    | INotification[]
    | INotification
) {
  this.$store.dispatch("notifications/addFromApiResponse", notification);
};

new Vue({
  router,
  store,
  i18n,
  render: h => h(App)
}).$mount("#app");
