import { Module, GetterTree, MutationTree, ActionTree } from "vuex";
import { IRootState } from ".";
import Vue from "vue";
import { AxiosResponse, AxiosError } from "axios";

enum INotificationType {
  success = "success",
  error = "error",
  info = "info",
  warning = "warning"
}

export interface INotification {
  type: INotificationType;
  name: string;
  args?: { [index: string]: string };
  seen: boolean;
  key: number;
}

interface INotificationState {
  notifications: INotification[]; // new notifications are appended
}

const state: INotificationState = {
  notifications: [
    {
      type: INotificationType.info,
      name: "api.info.hello-there",
      seen: false,
      key: Math.random()
    }
  ]
};

const mutations: MutationTree<INotificationState> = {};

const getters: GetterTree<INotificationState, IRootState> = {
  notificationHistory(state) {
    return [...state.notifications].reverse();
  },
  currentNotification(state): INotification | false {
    const unseenNotifications = state.notifications.filter(n => !n.seen);
    return unseenNotifications[0] || false;
  }
};

export interface IServerResponse {
  [index: string]: any;
  messages: ({
    name: string;
    args?: { [index: string]: string };
  })[];
}

const actions: ActionTree<INotificationState, IRootState> = {
  async add({ commit, state }, notification: INotification) {
    state.notifications.push(notification); // TODO make async and resolve when user clicks close
    if (state.notifications.length > 50) {
      state.notifications.shift();
    }
    state.notifications = state.notifications;
  },
  async closeCurrent({ state }) {
    for (let i = 0; i < state.notifications.length; i++) {
      if (!state.notifications[i].seen) {
        Vue.set(state.notifications[i], "seen", true);
        state.notifications = state.notifications; // temporary
        return;
      }
    }
    state.notifications = state.notifications; // temporary
  },
  async addFromApiResponse(
    { state, dispatch },
    data:
      | AxiosResponse<IServerResponse>
      | AxiosError
      | IServerResponse
      | INotification[]
      | INotification
  ) {
    let messages: INotification[] = [];
    const anyData: any = data;
    if (anyData.data) {
      messages = (anyData as AxiosResponse<IServerResponse>).data.messages.map(
        m => ({
          type: INotificationType.info,
          name: m.name,
          args: m.args,
          seen: false,
          key: Math.random()
        })
      );
    } else if (anyData.response && anyData.response.data) {
      messages = ((anyData as AxiosError).response!
        .data as IServerResponse).messages.map(m => ({
        type: INotificationType.info,
        name: m.name,
        args: m.args,
        seen: false,
        key: Math.random()
      }));
    } else if (anyData.messages) {
      messages = (anyData as IServerResponse).messages.map(m => ({
        type: INotificationType.info,
        name: m.name,
        args: m.args,
        seen: false,
        key: Math.random()
      }));
    } else if (anyData.length && anyData.map) {
      messages = (anyData as INotification[]).map(m => ({
        type: INotificationType.info,
        name: m.name,
        args: m.args,
        seen: false,
        key: Math.random()
      }));
    } else if (anyData.name) {
      messages = [
        {
          ...(anyData as INotification),
          type: INotificationType.info,
          seen: false,
          key: Math.random()
        }
      ];
    } else {
      console.log(anyData);
      alert("Unknown error");
    }
    // messages = data.response.data.messages; // TODO
    const type = INotificationType.error;
    if (messages && messages.length) {
      for (const message of messages) {
        dispatch("add", { ...message, type } as INotification);
      }
    }
    console.log(data);
  }
};

export const notificationModule: Module<INotificationState, IRootState> = {
  namespaced: true,
  state,
  mutations,
  getters,
  actions
};
