import { Module, GetterTree, MutationTree, ActionTree } from "vuex";
import { IRootState } from ".";
import * as api from "./api";

interface IAuthState {
  token: string;
  username: string;
}

const wait = (time: number) =>
  new Promise(resolve => setTimeout(resolve, time));

const state: IAuthState = {
  token: "", // the server will return a token
  username: ""
};

const mutations: MutationTree<IAuthState> = {
  setServerResponse(state, { token, username }) {
    state.token = token;
    state.username = username;
  },
  deleteAuthData(state) {
    state.token = state.username = "";
  }
};

const getters: GetterTree<IAuthState, IRootState> = {
  isLoggedIn(state) {
    return !!(state.token && state.username);
  },
  username(state) {
    return state.username;
  }
};

const actions: ActionTree<IAuthState, IRootState> = {
  async authenticate({ commit }, { password, username }) {
    const data = await api.login({ username, password });
    commit("setServerResponse", {
      token: (data as any).payload.token, // TODO:
      username
    });
    return data;
  },
  async logout({ commit }) {
    // TOOD: tell the server to invalidate the token
    await wait(500);
    commit("deleteAuthData");
    // TODO: also delete jobs, repositories and settings still saved in the store
  },
  async createUser(
    { commit, dispatch },
    { password, username, firstName, lastName, email }
  ) {
    const data = await api.createUser({
      password,
      username,
      firstName,
      lastName,
      email
    });
    return data;
  }
};

export const authModule: Module<IAuthState, IRootState> = {
  namespaced: true,
  state,
  mutations,
  getters,
  actions
};
